<template>
    <div class="view-style" @click="playgame">
        <div class="load" :style="`background-image:url(${require('@/assets/load.webp')})`"></div>
        <div class="load-text">{{msg}}</div>
    </div>
</template>

<script>
import { encryptLong, decryptLong } from '@/utils/jsencrypt';

export default {
    name:"TransitView",
    data(){
        return{
            msg:"点击进入游戏"
        }
    },
    mounted(){
        // setTimeout(() => {
        //     this.$router.push({name:"game"})
        // }, 5000);
        // this.init()
    },
    methods:{
        playgame(){
            const idx = this.msg.indexOf("点击进入游戏",0),
                content = "点击进入游戏";
            if(idx !== -1 && this.msg.slice(idx,idx + content.length) == content){
                this.$router.push({name:"game"})
            }
        },
        init(){
            const archive = JSON.parse(decryptLong(localStorage.getItem("archive")))
            console.log(archive);
            if(typeof archive.valueOf() !== "object"){
                this.msg = "点击进入游戏"
            }else{
                this.msg = "存档验证中"
                this.falseValueByObject(archive)
            }
        },
        // 存档验证
        falseValueByObject(obj){
            try {
                Object.keys(obj).map((key)=>{
                    if(obj[key] in [undefined,"undefined",null,"null",NaN,"NaN",""] == true){
                        throw Error
                    }else{
                        if(Object.prototype.toString.call(obj[key]) == "[object Object]"){
                            this.falseValueByObject(obj[key])
                        }else{
                            return true;
                        }
                    }
                })
            } catch (error) {
                this.msg = "存档已损坏，点击进入游戏，开始新游戏"
            }
            
        }
    }
}
</script>

<style lang="scss" scoped>
.view-style{
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.85);
}
.load{
    position: absolute;
    left: calc(50% - 150px);
    top: 20%;
    width: 300px;
    height: 300px;
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    animation: load 20s linear infinite;
}
@keyframes load {
    0% {rotate:0deg;}
    100% {rotate:360deg;}
}
.load-text{
    position: absolute;
    width: 100%;
    text-align: center;
    font-size: 30px;
    font-family: "幼圆", Courier, monospace;
    color: aliceblue;
    letter-spacing: 10px;
    text-indent: 10px;
    top: calc(20% + 350px);
}
</style>